import React from 'react';
import s from './filterButton.module.css'

type filterButtonProps = {
  todoFilter: Function 
}

const FilterButton:React.FC<filterButtonProps>=({todoFilter})=> {
  return (
    <div className={s.filter}>
      
      <button className={s.button} onClick={()=>todoFilter('all')}></button>
      <button className={s.button} onClick={()=>todoFilter(true)}></button>
      <button className={s.button} onClick={()=>todoFilter(false)}> </button>
    </div>
  );
}

export default FilterButton;